
































































import Vue from 'vue';
import { isLoggedIn, logout } from '@/services/auth';

export default Vue.extend({
  name: 'AppWrapper',

  methods: {
    handleLogout() {
      logout();
      this.$router.replace('/login');
    }
  },

  mounted () {
    if (!isLoggedIn()) {
      this.$router.replace('/login');
    }
  }
});
